import nodesNames from './nodesNames'
import nodesValues from './nodesValues'


const genRandomRgbaColor = (opacity) => {
    //let colorValue = Math.round(Math.random() * 122) + 123;
    //return `rgba(${colorValue}, ${colorValue}, ${colorValue} , ${opacity})`
    return `rgba(${Math.round(Math.random() * 122) + 123}, ${Math.round(Math.random() * 122) + 123}, ${Math.round(Math.random() * 122) + 123} , ${opacity})`;
}
const dummyData = [];
const render_nodesNames = nodesNames;
const render_nodesValues = nodesValues;

render_nodesNames.forEach((nodeName, index) => {
    dummyData.push(
        {


            label: `${nodeName.split("_").join(" ").charAt(0).toUpperCase() + nodeName.slice(1).split("_").join(" ")}`,
            id: +index++,
            active: (index >= 6
                ? false
                : true),
            lineTension: 0,
            data: render_nodesValues[index],
            fill: true,
            hidden: false,
            backgroundColor: genRandomRgbaColor('.2'),
            borderColor: genRandomRgbaColor('1'),
            borderWidth: 3,
            yAxisID: 'y-axis-1',
        })
});




//




export default dummyData;


